#searchBox-results {
    position: relative;
    margin-top: 3%;
    max-width: 45%; 
    transform: translate(-50%, -50%);
    height: 2.5rem;
    background: white;
    border-radius: 40px;
    padding: 1px;
    margin-left: 50%;
    display: flex;
    box-shadow: 0px 0px 20px lightgray;
    border: 0.5px solid #696969;
}

#search-results {
    width: 92%;
    padding: 0%;
    border-radius: 40px;
    position: relative; 
    border: none;
    outline: none;
    background: white;
    float: left;
    padding-left: 2%;
    overflow: visible;
    transition: 0.4s;
    line-height: 2.4rem;
    display: flex;
}  

.searchIcon-results {
    color: #696969;
}

.searchIcon-results:hover {
    color: #696969;
}

::placeholder {
    color: grey;
    opacity: 0.5;
}

#buttonSearch-results {
    float: right;
    width: 2.5rem;
    height: 2.35rem;
    border-radius: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #F4C557; */
    margin-left: 2%;
}

#filter-options {
    align-items: center;
    justify-content: center;
    /* padding-top: 0%; */
    margin-right: auto;
    margin-left: auto;
    padding: 1%;
    display: flex;
}

#filter-equipment, #filter-ingredients, #reset {
    text-align: center; 
    padding-right: 5%;
}

#reset {
    background-color: #F4C557;
    border-radius: 50px;
    text-align: center;
    width: 0;
    padding-left: 1.2%;
    padding-top: 0.5%;
    padding-bottom: 0.5%;
}

#reset > p {
    vertical-align: center;
}

#card-text {
    text-align: center;
    margin: 0;
}

#results-card-body {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
}

#recipe {
    padding-bottom: 3%;
}

.recipe-image {
    width: 100%;
    overflow: hidden;
    display:table-cell; 
    vertical-align:middle; 
    text-align:center
}

.recipe-image img {
    height: 231px;
}

.results-card-title {
    padding-top: 5%;
}

#results {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3%;
}

.results-card, .recipe-image {
    border-radius: 30px;
}

.results-card {
    height: 400px;
    margin-bottom: 2%;
}


.blur {
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
    background-color: black;
}

.post_options {
    max-width: 100%;
    /* height: 50%; */
    float: left;
    background-color: white;
    position: absolute;
    z-index: 1;
    width: 45%;
    margin-top: 4rem;
    margin-left: -13%;
    border: 1px solid lightgrey;
    border-radius: 10px;
    margin-bottom: 4rem; 
    /* filter: blur(5px); */
}

#checkBox {
    text-align: left;
    color: black;
}

#checkBoxes {
    padding-left: 3%;
    padding-bottom: 2%;
    padding-top: 2%;
    display: block;
}

#filter-done-button {
    background-color: #5EB003;
    color: white;
    margin-top: 5%;
    padding: 1%;
    width: 30%;
    float: right;
    margin-right: 5%;
}